body{
	margin: 0;
	padding: 0;
	height: 100%;
	min-height: 100%;
	background-color: #DDDCDC;
}

.searchRecipeContainer>h1{
	font-size: 300%;
	text-align: justify-all;
}

.searchRecipeContainer>input[type='text']{
	width: 40%;
	padding: 5px;
	font-size: 200%;
	border: none;
	box-shadow: 0px 5px 10px rgba(0,0,0,0.3);
}
.searchRecipeContainer>input[type='text']:focus{
	box-shadow: 0px 0px 10px inset;
}

.searchRecipeContainer>input[type='button']{
	width: 20%;
	font-size: 200%;
	border: none;
	border-radius: 15px;
	background-color: #FFC81D;
	cursor: pointer;
	-webkit-transition: 0.3s all ease-in-out;
	transition: 0.3s all ease-in-out;
}

.searchRecipeContainer>input[type='button']:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	box-shadow: 0px 5px 35px rgba(0,0,0,0.3);
}

@media only screen and (max-width: 1024px){
	.searchRecipeContainer>h1{
		font-size: 150%;
		margin-bottom: 20px;
	}
	.searchRecipeContainer>input[type='text']{
		width: 90%;
	}
	.searchRecipeContainer>input[type='button']{
		width: 50%;
		margin-bottom: 30px;
	}
}
.grid-container {
  display: grid;
  height: 100%;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 0.5fr;
  grid-gap: 1px 1px;
  grid-template-areas: "img title" "desc desc";
  text-align: left;
  background: white;
  margin: 25px;
  box-shadow: inset -20px 2px 500px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}

.img { 
	grid-area: test; 
}
.img-start-cell>img{
	width: 20vw;
	height: auto;
	margin: 30px;
	border-radius: 15px;
	box-shadow: 15px 20px 100px 0px black;
}

.title { 
	grid-area: title; 
	margin: 10px;
}
.title>h2{
	font-size: 2vw;
	text-align: right;
	border-bottom: 2px red solid;
}

.desc { 
	grid-area: desc; 
	font-size: 1vw;
	margin: 10px;
}

.title>div>p{
	list-style-type: none;
	margin: 0px 5px;
	display: inline;
}

@media only screen and (max-width: 1024px){
	.grid-container {
	  display: grid;
	  height: 100%;
	  width: 100%;
	  margin: auto;
	  grid-template-columns: 100%;
	  grid-template-rows: 0.5fr 0.1fr 1fr;
	  grid-gap: 1px 1px;
	  grid-template-areas: "img" "title" "desc";
	  text-align: left;
	  margin-bottom: 20px;
	  background: white;
	  box-shadow: inset -20px 2px 200px rgba(0, 0, 0, 0.5);
	}
	.img-start-cell{
		text-align: center;
	}
	.img-start-cell>img{
		width: 80%;
	}
	.title{
		margin: 0;
	}

	.title>h2{
		font-size: 6vw;
		margin: 0;
		text-align: center;
	}

	.desc{
		font-size: 4vw;
		text-align: justify;
	}
}

.listContainer{
	background: #FF9330;
	margin: 10px 10px 100px 10px;
	padding: 10px;
}
.Navigation{
	padding-bottom: 80px;
}

.Navigation>input{
	font-size: 200%;
	margin: 10px;
	border-radius: 10px;
	background-color: orange;
	border: none;
	cursor: pointer;
}
#current{
	cursor: default;
	padding: 0 10px;
}
.gridFooter{
	display: inline-grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr; 
	grid-template-areas: 'none author Icons';
}
.author{
	grid-area: author;
}

footer>div>div{
	font-size: 23px;
	background-color: gray;
	text-align: center;
	position: fixed;
    bottom: 0;
    width: 100%;
}

footer>div>img{
	position: fixed;
	bottom: 63px;
	left: 20px;
	-webkit-animation-name: donut-animation;
	        animation-name: donut-animation;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}
.attribution{
	font-size: 30%;
	text-align: right;
	color: #525252;
	grid-area: Icons;
}

@-webkit-keyframes donut-animation{
	0% {
		bottom: 0px; 
	}
	5%{
		bottom: 0px;
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
	}
	18% {
		bottom: 160px; 
		-webkit-animation-timing-function: ease-in; 
		        animation-timing-function: ease-in;
	}
	31% {
		bottom: 63px; 
		-webkit-animation-timing-function: ease-out; 
		        animation-timing-function: ease-out;
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}
	33% {
		-webkit-transform: scale(1.2, 0.9);
		        transform: scale(1.2, 0.9);
	}
	45% {
		bottom: 120px; 
		-webkit-animation-timing-function: ease-in; 
		        animation-timing-function: ease-in;
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}
	60% {
		bottom: 63px; 
		-webkit-animation-timing-function: ease-out; 
		        animation-timing-function: ease-out;
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}
	64%{
		-webkit-transform: scale(1.2, 0.9);
		        transform: scale(1.2, 0.9);
	}
	75% {
		bottom: 100px; 
		-webkit-animation-timing-function: ease-in; 
		        animation-timing-function: ease-in;
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}
	85%{
		bottom: 63px;
	}
	95%{
		bottom: 0px;
	}
	100% {
		bottom: -10px; 
		-webkit-animation-timing-function: ease-out; 
		        animation-timing-function: ease-out;
	}
}

